.Loader {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.Loader:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 4px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: Loader 1.2s linear infinite;
}
@keyframes Loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px;
}